"use client";

import { useState, useEffect } from "react";
import ModeToggle from "@/components/ModeToggle";

export default function Home() {
  const [quote, setQuote] = useState("");
  const [author, setAuthor] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const fetchQuote = async () => {
    setLoading(true);
    setError("");
    try {
      const res = await fetch("/api/quotes");
      if (!res.ok) throw new Error("Failed to fetch quote");
      const data = await res.json();
      setQuote(data.content);
      setAuthor(data.author);
    } catch (err) {
      console.error(err);
      setError("Oops! Could not load quote.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchQuote();
  }, []);

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(`"${quote}" — ${author}`);
      alert("Copied to clipboard!");
    } catch {
      alert("Failed to copy.");
    }
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center px-4 py-8 bg-white dark:bg-gray-900 text-gray-900 dark:text-white transition-colors">
      <div className="w-full max-w-2xl space-y-8">
        <header className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Quote Generator</h1>
          <ModeToggle />
        </header>

        <section className="bg-gray-100 dark:bg-gray-800 rounded-xl p-6 shadow-md">
          {loading ? (
            <p className="text-center text-gray-500 dark:text-gray-400">Loading...</p>
          ) : error ? (
            <p className="text-center text-red-500">{error}</p>
          ) : (
            <>
              <p className="text-xl italic text-center mb-4">“{quote}”</p>
              <p className="text-right font-medium text-indigo-600 dark:text-indigo-400">
                — {author}
              </p>
            </>
          )}
        </section>

        <div className="flex gap-4">
          <button
            onClick={fetchQuote}
            disabled={loading}
            className="flex-1 bg-indigo-600 text-white py-2 px-4 rounded-lg hover:bg-indigo-700 disabled:opacity-50 transition"
          >
            New Quote
          </button>
          <button
            onClick={copyToClipboard}
            disabled={loading || !quote}
            className="flex-1 bg-gray-300 dark:bg-gray-700 text-gray-800 dark:text-gray-100 py-2 px-4 rounded-lg hover:bg-gray-400 dark:hover:bg-gray-600 disabled:opacity-50 transition"
          >
            Copy
          </button>
        </div>
      </div>
    </main>
  );
}
